body {
    background: linear-gradient(180deg, #f3f3fb 0%, #fdfbfd 100%);
}
* {
    scrollbar-width: thin;
}
::-webkit-scrollbar {
    width: 5px; /* width of the entire scrollbar */
    scroll-margin-right: 10px;
    scroll-margin-left: 10px;
    cursor: pointer;
}
::-webkit-scrollbar-track {
    background: none; /* color of the tracking area */
    border-radius: 10px;
}
::-webkit-scrollbar-thumb {
    border: 5px solid transparent;
    background: #b7bdcb;
    box-shadow: 4px 4px 15px rgba(112, 124, 151, 0.05), 2px 2px 10px rgba(112, 124, 151, 0.1),
        1px 1px 50px rgba(112, 124, 151, 0.15);
    border-radius: 10px;
    height: 80px;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

::-webkit-scrollbar-thumb:hover {
    background: #888b94;
}

* {
    padding: 0;
    margin: 0;
    border: 0;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

:focus,
:active {
    outline: none;
}

a:focus,
a:active {
    outline: none;
}

nav,
footer,
header,
aside {
    display: block;
}

html,
body {
    overflow: hidden;
    font-family: "Proxima Nova";
    height: 100vh;
    width: 100vw;
    font-size: 100%;
    line-height: 1;
    font-size: 14px;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
    font-family: inherit;
}

input::-ms-clear {
    display: none;
}

button {
    cursor: pointer;
}

button::-moz-focus-inner {
    padding: 0;
    border: 0;
}

a,
a:visited {
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

ul li {
    list-style: none;
}

img {
    vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: inherit;
    font-weight: inherit;
}

details {
    list-style: none;
}

